*{
    font-size: 20px;
    font-weight: 800;
}
html{
    background:  linear-gradient(135deg, #1b012e, #180430, #0e0d33, #001434, #001a34, #001e31, #00202f, #00212e);;
}
body{
    background: linear-gradient(135deg, #1b012e, #180430, #0e0d33, #001434, #001a34, #001e31, #00202f, #00212e);
}
.row{
width: min(90%,1400px);
background-color: rgb(240,240,240,0.5);

padding: 20px;
border-radius: 10px;
margin: auto;
margin-bottom: 40px;
margin-top: 40px;
}
.TextArea{
 font-size: 20px;
 color:black;
 font-weight: 800;
 padding: 20px;

}
.Navbar{
    background-color: rgb(0,0,0,0.8);
 color:#00beed;
 padding: 20px;
 font-size: 20px;
 font-weight: 800;
box-shadow: 0 4px 8px black;
}
.logo{
    display: inline;
}
.Navbar li{
    display: inline;
    padding: 20px;
    color:#00beed;
    transition: 0.15s;
}
.Navbar li:hover{
    opacity: 0.8;
    font-size: 1.2em;
}
.Navbar ul{
    display: inline;
}
.Navbar button{
    border: none;
    font-size: 20px;
    background-color:#141414;
    padding: 10px;
    color:#00beed;
    box-shadow: 0 4px 8x black;
    border-radius: 10px;
}
.Navbar input[type="text"]{
    width: 5%;
    margin-left: 5%;
    margin-right: 5%;
    font-size: 20px;
    color:#00beed;
    background-color: #141414;
}
.Progress{
    height: 300px;
    width: 150px;
    border-radius: 10px;
  opacity: 0.5;
    display: inline-block;
    
    
}

.TextArea{

}
.TypingArea input[type = "text"]{
    width: 90%;
    height: 100px;
    font-size: 20px;
    color:green;
    border-radius: 20px;
    border: none;
    opacity: 0.5;
}
.TypingArea input[type = "text"]:focus{
    border: none;
}
.Navbar .active{
    color: white;
}
.Card{
    display: inline;
}
#card{
    display: inline;
}
.progresscontainer{
    width: 50%;
    padding: 20px;
    display: inline;
}
.red{
    color:red;
    text-decoration: underline;
}
.green{
    
}
.syncbutton{
color:#00beed;
}
.syncbutton:hover{
    transform: scale(1.1);
    color:black;
}